<template lang="pug">
.auth__wrapper
  .auth__card
    p.auth__notaccount Еще нет аккаунта? <router-link to="/registr">Зарегистрируйтесь</router-link>

  .auth__card
    .auth__card-inner
      .app__header-logo
        img(src="@/assets/images/logo.png")
      .auth__form
        h1.auth__title Авторизация
        .auth__selector
          button.auth__selector-btn(@click="() => (ul = false)")(
            :class="{ active: !ul }"
          ) Физическим лицам
          button.auth__selector-btn(@click="() => (ul = true)")(
            :class="{ active: ul }"
          ) Юридическим лицам
        ul.auth__error(v-if="errors")
          li.error(v-for="error in errors") {{ error }}
        .auth__fieldset(v-if="ul")
          //- .auth__fieldset-item
            Input(label="ИНН" v-model="inn")
          .auth__fieldset-item
            Input(label="Логин", v-model="login")
          .auth__fieldset-item
            Input(
              label="Пароль",
              v-model="password",
              type="password",
              :password="true"
            )
        .auth__fieldset(v-if="!ul")
          .auth__fieldset-item
            Input(label="Номер телефона", v-model="login")
          .auth__fieldset-item
            Input(
              label="Пароль",
              v-model="password",
              type="password",
              :password="true"
            )

        .auth__submit
          button.button(@click="submit", v-if="!loading") Войти
          button.button(v-else) Подождите...
  br
  br      
</template>

<script>
import { Icon, Input, Checklist } from "@/components";
import Vue from "vue";

export default {
  name: "auth",
  components: {
    Icon,
    Input,
    Checklist,
  },
  data() {
    return {
      ul: true,
      inn: "",
      login: "",
      password: "",
      errors: [],
      loading: false,
      phone: "",
    };
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.cuser.isLoggedIn;
    },
  },
  mounted() {
    if (this.isLoggedIn) {
      console.log("Вы авторизованы, покиньте страницу...");
    }
  },
  methods: {
    checkin() {
      this.errors = [];
      // if (this.inn.length <= 10) {
      //     this.errors.push('Введите корректный ИНН')
      // }
      if (this.login.length < 3) {
        this.errors.push("Введите корректный логин");
      }
      if (this.password.length < 3) {
        this.errors.push("Введите корректный пароль");
      }
    },
    async submit() {
      this.loading = true;
      this.checkin();

      if (this.errors.length === 0) {
        const userObject = {
          inn: this.inn,
          login: this.login,
          password: this.password,
        };
        await this.$store.dispatch("authUser", userObject).then((res) => {
          this.$router.push("/");
          this.errors.push(res);
        });
      }
      this.loading = false;
    },
  },
};
</script>

<style scoped lang="sass">
@import ~@/assets/styles/vars

.auth
    display: flex
    flex: 1
    height: 100%
    align-items: center
    justify-content: center
    background: #F3F6FA

    &__container
        max-width: 360px
        width: 100%
    &__wrapper
        display: flex
        flex-direction: column
    &__card
        background: #FFFFFF
        box-shadow: 0 2px 4px #E7EDF6
        border-radius: 4px
        margin-bottom: 16px
        &-inner
            margin: 24px
            display: flex
            flex-direction: column
    &__notaccount
        font-weight: 500
        font-size: 15px
        line-height: 24px
        color: #7F8DA8
        margin: 16px
        text-align: center
    &__title
        font-style: normal
        font-weight: 500
        font-size: 15px
        line-height: 24px
        color: #7F8DA8
        margin: 8px 0 20px
        text-align: center
    &__form
        display: flex
        flex-direction: column
    &__selector
        display: flex
        border-radius: 4px
        overflow: hidden
        margin-bottom: 16px
        &-btn
            display: flex
            flex: 1
            justify-content: center
            align-items: center
            font-style: normal
            font-weight: normal
            font-size: 13px
            line-height: 24px
            color: #7F8DA8
            background: #F3F6FA
            border: 0
            cursor: pointer
            outline: none
            padding: 8px

            &.active
                background: #3F64A9
                color: #fff
    &__fieldset
        display: flex
        flex-direction: column
        margin-bottom: 16px
        &-item
            margin-bottom: 16px
            &:last-child
                margin-bottom: 0
    &__error
        margin: 0 0 8px
.error
    color: red
    margin: 0
    line-height: 16px
    font-size: 13px
    &:last-child
        margin-bottom: 0
.app__backward
    margin-bottom: 16px
    line-height: 48px
.app__header-logo
    width: 100%
    padding: 0

.button
    background: $color-primary
    height: 3rem
    cursor: pointer
    font:
        weight: 500
        size: .95rem
    color: #fff
    border-radius: 4px
    padding: 2px 1rem 0
    display: flex
    justify-content: center
    align-items: center
    border: 2px solid transparent
    width: 100%
</style>
