<template lang="pug">
.auth
    .auth__container
        .auth__wrapper
            .auth__card
                p.auth__notaccount Уже зарегистрированы? <router-link to="/auth">Войдите</router-link>

            .auth__card
                .auth__card-inner
                    .app__header-logo
                        img(src="@/assets/images/logo.png")
                    .auth__form(v-if="!sent")
                        h1.auth__title Регистрация
                        .auth__selector
                            button(@click="()=>ul=false").auth__selector-btn(:class="{'active': !ul}") Физическим лицам
                            button(@click="()=>ul=true").auth__selector-btn(:class="{'active': ul}") Юридическим лицам
                        ul.auth__error(v-if="errors")
                            li(v-for="error in errors" class="error") {{ error }}
                        .auth__fieldset(v-if="ul")
                            .auth__fieldset-item
                                Input(label="Номер телефона" type="phone" v-model="phone")
                            .auth__fieldset-item
                                Checklist(
                                    :items="[`Вы принимаете условия ${'пользовательского соглашения'}`]",
                                    type="checkbox"
                                    itemsColor="font"
                                    v-model="checkbox"
                                )
                                                        
                        .auth__fieldset(v-if="!ul")
                            .auth__fieldset-item
                                Input(label="Номер телефона" type="phone" v-model="phone")
                            .auth__fieldset-item
                                Checklist(
                                    :items="[`Вы принимаете условия ${'пользовательского соглашения'}`]",
                                    type="checkbox"
                                    itemsColor="font"
                                    v-model="checkbox"
                                )                                
                        .auth__submit
                            button(class="button" @click="submit" v-on:click="()=>sent=true" v-if="!loading") Зарегистрироваться
                            button(class="button" v-else) Подождите...
                    .auth__form(v-else)
                        h1.auth__title Регистрация
                        p.auth__text На ваш мобильный телефон отправлен СМС с паролем для входа в личный кабинет. При необходимости вы можете поменять пароль в личном кабинете абонента
                        ul.auth__error(v-if="errors")
                            li(v-for="error in errors" class="error") {{ error }}                    
                        .auth__fieldset
                            .auth__fieldset-item
                                Input(label="Номер телефона" type="phone" v-model="phone")
                            .auth__fieldset-item
                                Input(label="Пароль" v-model="password" type="password")                     
                        .auth__submit
                            button(class="button" @click="submit" v-if="!loading") Войти
                            button(class="button" v-else) Подождите...                            
</template>

<script>
import {
    Icon,
    Input,
    Checklist,
} from '@/components'
import Vue from "vue";

export default {
    name: "registr",
    components: {
    Icon,
    Input,
    Checklist,
},
    data() {
        return {
            ul: true,
            sent: false,
            inn: '',
            errors: [],
            loading: false,
            phone: '',
            password: '',
            checkbox: false,
        }
    },
    computed: {
        isLoggedIn () {
            return this.$store.getters.cuser.isLoggedIn
        }
    },
    mounted() {
        if (this.isLoggedIn) {
            console.log('Вы авторизованы, покиньте страницу...')
        }
    },
    methods: {
        checkin () {
            this.errors = []
            if (this.inn.length <= 10) {
                this.errors.push('Введите корректный ИНН')
            }
            if (this.login.length < 3) {
                this.errors.push('Введите корректный логин')
            }
            if (this.password.length < 3) {
                this.errors.push('Введите корректный пароль')
            }
        },
        async submit () {
            /* this.loading = true 
            this.checkin()

            if (this.errors.length === 0) {
                const userObject = {
                    inn: this.inn,
                    login: this.login,
                    password: this.password,
                }
                await this.$store.dispatch('authUser', userObject)
                    .then(res => {
                        this.errors.push(res)
                    })

            } */
            this.loading = false
        }
    }
}
</script>

