<template lang="pug">
.support-create
    .support__mobile-back(v-if="isMobile")
        router-link(to="/support")
            img(src="@/assets/images/back-triangle.svg")
            span Назад
    .layout__title.support-create__title
        Icon(icon="new-message" color="primary")
        span Новое обращение

    .support-create__category
        Input(type="select" :options="categories" label="Выберите категорию обращения" optionLabel="label" v-model="selectedCategory" style="z-index:1")

    .support-create__form
        .support-create__form-placeholder(v-if="message === ''") Напишите сообщение...
        label(for="file").file
            input(type="file" id="file" name="files[]" :value="file" @change="filesChange" multiple).file-input
            Icon.file-icon(icon="attachment" color="primary" )
        .support-create__form-input(contenteditable @input="onChangeMessage" ref="chatinput")
        Icon(icon="send" color="primary" @click.native="sendMessage")
    .file-items
        .file-item(v-for="el in files") 
            p.file-text {{el.name}} 
            span.file-icon-wrapper(@click="filesRemove(el.name)")
                Icon(icon="close").file-icon        
</template>

<script>
import {
    Icon, Input
} from '@/components'

export default {
    name: 'SupportCreate',
    data () {
        return {
            selectedCategory: '',
            categories: [
                { label: 'Вопросы по газовым заправкам АО «Сахатранснефтегаз»', value: 0 },
                { label: 'Какая-то категория', value: 1 }
            ],
            message: '',
            files: [],
            file: null,
        }
    },
    methods: {
        onChangeMessage (e) {
            this.message = e.target.innerText
        },
        filesChange (e) {
            this.files.push(e.target.files[0])
        },
        filesRemove (v) {
            let index = this.files.indexOf(v);
            this.files.splice(index, 1);
            console.log(v, 'test', this.files)
        },
        sendMessage () {
            if (this.message !== '') {
                
            }
        }
    },
    setup() {
       /*  const files = ref([])
        const filesChange = (e) =>{
            console.log(e.target, 'test files target')
        }
        return {
            filesChange,
            files,
        }    */
    },
    components: {
        Icon, Input
    },
    computed: {
        isMobile () {
            return screen.width < 760
        }
    },
}
</script>

<style lang="sass">
@import @/assets/styles/vars

.file-input[type="file"] 
    display: none
.file-items
    display: flex
    padding: 0.5rem 0.5rem
    margin-left: 4.1rem
    flex-wrap: wrap
    .file-item
        display: flex
        padding: 8px 10px 8px 18px 
        justify-content: space-between
        background: #F3F6FA
        border-radius: 4px
        margin-right: 7px
        margin-top: 7px
        .file-text
            color: #7F8DA8
            word-break: break-all
            font-size: 13px
            font-weight: 400
            line-height: 24px
            margin: 0
        .file-icon-wrapper
            display: flex
            justify-content: center
            height: 100%
            margin-left: 15px    
     
.support-create
    @media screen and (max-width: $mobile-width)
        display: flex
        flex-direction: column
        height: calc(100vh - 56px)

    &__title
        padding: 1.5rem 1.5rem 0

        @media screen and (max-width: $mobile-width)
            padding: 16px 16px 0
            flex-shrink: 0

    &__category
        padding: 1.5rem
        @media screen and (max-width: $mobile-width)
            flex-grow: 1
            padding: 16px

    &__form
        border-top: 1px solid $color-border
        display: flex
        justify-content: space-between
        align-items: flex-start
        padding: 1rem 1.5rem
        position: relative

        @media screen and (max-width: $mobile-width)
            flex-shrink: 0

        &-placeholder
            position: absolute
            font-size: 13px
            line-height: 1.5rem
            left: 4.1rem
            z-index: -1
            color: $color-label

        .icon
            margin-top: .2rem
            cursor: pointer
        
        &-input
            width: 100%
            margin: 0 1.5rem
            font-size: 13px
            line-height: 1.5rem

            &:focus
                outline: none
</style>

